import React, {useContext} from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import Grid from '@material-ui/core/Grid';
import Button from '@material-ui/core/Button';
import AppContext from "../context/index";
import BlankSpace from './BlankSpace';
import Skeleton from '@material-ui/lab/Skeleton';
const useStyles = makeStyles({
//SongCard (sc) Styles
  scroot: {
    width: 600,
    minHeight: 100,
    flex: 'wrap',
  },
  scpos: {
    marginBottom: 12,
  },
  sectionheaders:{
    margin: "45px 30px 45px 30px",
    color: "#9a9a9a",
    fontSize: ".75rem",
    letterSpacing: ".04em",
    fontWeight: 400,
  },
  outputtext: {
    backgroundColor: "#FFFF00"
  },
  lyricsAndPassword: {
    fontWeight: 100,
    wordBreak: "break-word",
    lineHeight: "1.7em",
    fontStyle: "italic",
  },
  passwordOut: {
    fontSize: "1rem",
    fontWeight: 400,
    lineJeight: 1,
    letterSpacing: "1px",
    fontStyle: "bold",
  },
});

export default function SongCard() {
  const classes = useStyles();

  const appContext = useContext(AppContext);
  const {
    albumArt,
    title,
    artist,
    loading,
    setLoading,
    open,
    setOpen,
    music,
    musician,
    password,
    songLyrics,
  } = appContext;

  return (
    <Grid
  container
  direction="column"
  justify="flex-start"
  alignItems="center"
  overflow="hidden"
>
    <Grid item>
    <Card className={classes.scroot}>
    <Typography variant="overline" className={classes.sectionheaders}>
        Lyrics
      </Typography>
      <CardContent>
    <Typography variant="body1" className={classes.lyricsAndPassword}>{songLyrics}</Typography>
      </CardContent>
      <CardActions className={classes.scpos} />
    </Card>
    </Grid>
    <BlankSpace />
    <Grid item>
    <Card className={classes.scroot}>
    <Typography variant="overline" className={classes.sectionheaders}>
        Password
      </Typography>
      <CardContent>
       <Typography variant="body1" className={classes.passwordOut}>{password}</Typography>
      </CardContent>
      <CardActions className={classes.scpos} />
    </Card>
    </Grid>
    </Grid>
  );
}